import React, { useState } from "react";
import SVG from "react-inlinesvg";
import { Layout } from "@/views/layout";
import DisabledInput from "@/components/disabledInput/index";
import {
  Accordion,
  Tabs,
  Table,
  ContractCards,
  Button,
} from "@/components";
import { history } from "@/helpers";
import TooltipText from "public/tooltipText.svg";
import "./style.scss";

const faqItems = [
  {
    title: "Какие кредиты я могу оплатить?",
    text: "Все виды займов оформленные в АО «Банк Фридом Финанс Казахстан» или в ТОО «Микрофинансовая организация ФРИДОМ ФИНАНС Кредит», включая потребительские кредиты, ипотечные кредиты, предоставленные на товар в магазине, включая товар в рассрочку. ",
  },
  {
    title:
      "Я внес сумму больше чем ежемесячный платеж. Что произойдет с моими деньгами?",
    text: "Деньги останутся на вашем текущем счете до следующего планового платежа по графику.",
  },
  {
    title: "При оплате не приходит код подтверждения. Что делать?",
    text: "Проверьте Ваше подключение к сети и попробуйте повторить платеж через некоторое время. Если снова появится ошибка, обратитесь в банк, выпустивший вашу карту или к оператору сотовой связи. Если у вас есть другая карта, используйте ее. В случае дополнительных вопросов можете обратиться в call-центр по номеру 595.",
  },
  {
    title: "Как получить чек/квитанцию платежа?",
    text: "Сразу после оплаты вы можете сохранить ее нажав на кнопку 'Скачать квитанцию.'",
  },
  {
    title: "Как быстро пройдет платеж? ",
    text: "Все ежемесячные оплаты принятые после 22:00 будут погашаться следующим днем. Досрочное и частично досрочное погашение принимается до 16:00. Чтобы не выйти на просрочку, необходимо вносить оплату в соответствии с расписанием платежей.",
  },
  {
    title: "Есть ли комиссии при оплате? ",
    text: "Комиссия взымается только в случае если Ваш договор предусматривает комиссию за досрочное погашение. ",
  },
  {
    title: "Как частично досрочно погасить кредит?",
    text: "Вы можете частично досрочно погасить на вкладке “Досрочное погашение”.  Выберите нужный договор, заполните поле с суммой.",
  },
];

const tabTitles = [
  {
    title: "Ежемесячный платеж",
    value: 0,
  },
  {
    title: "Досрочное погашение",
    value: 1,
  },
];

const tableTitles = [
  { name: "", field: "сhoosed_item", type: "checkbox" },
  { name: "Информация о договоре", field: "id" },
  {
    name: "Остаток задолженности",
    field: "borrower_full_name",
    type: "currency",
  },
  { name: "Ежемесячный платеж", field: "borrower_iin", type: "currency" },
  {
    name: "Штрафы,пени",
    field: "fines_payment",
    tooltipText: "Сумма штрафов и пени всегда включена в сумму общего платежа",
    type: "fines",
  },
  {
    name: "К оплате",
    field: "input_item",
    tooltipText:
      "Поле доступно для редактирования. Не забудьте внести сумму с учетом штрафов или пени, в случае их наличия",
    type: "input",
  },
  { name: "Оплатить до", field: "status" },
];

const tableTitles2 = [
  { name: "", field: "сhoosed_item", type: "checkbox" },
  { name: "Информация о договоре", field: "id" },
  {
    name: "Остаток задолженности",
    field: "borrower_full_name",
    type: "currency",
  },

  {
    name: "Штрафы,пени",
    field: "fines_payment",
    tooltipText: "Сумма штрафов и пени всегда включена в сумму общего платежа",
    type: "fines",
  },
  {
    name: "Сумма для частично досрочного погашения",
    field: "borrower_full_name",
    type: "currency",
  },
  {
    name: "К оплате",
    field: "input_item",
    tooltipText:
      "Поле доступно для редактирования. Не забудьте внести сумму с учетом штрафов или пени, в случае их наличия",
    type: "input",
  },
  {
    name: "Переплата по займу",
    field: "borrower_full_name",
    type: "currency",
  },
];


const PaymentPage = (props) => {

  const [totalForPayment, setTotalForPayment] = React.useState(0);
  const [tableData, setTableData] = useState([
    {
      id: "№ 135 от 12.12.2020 Ипотека",
      borrower_full_name: "100000",
      borrower_iin: "10000",
      fines_payment: "1000",
      input_item: "11000",
      сhoosed_item: false,
    },
    {
      id: "№ 135 от 12.12.2020 Товарный кредит",
      borrower_full_name: "120000",
      borrower_iin: "50000",
      fines_payment: "3000",
      input_item: "53000",
      сhoosed_item: false,
    },
    {
      id: "№ 135 от 12.12.2020 Ипотека",
      borrower_full_name: "140000",
      borrower_iin: "10000",
      fines_payment: "2000",
      input_item: "12000",
      сhoosed_item: false,
    },
  ]);

  const handleTableChange = (index, field, value) => {
    
    let newTableData = [];
    if (tableData.length > 0) {
      for (let i = 0; i < tableData.length; i++) {
        if (index !== i) {
          newTableData.push(tableData[i]);
        } else {
          newTableData.push({ ...tableData[i], [field]: value });
        }
      }

      let top = 0;
      if (newTableData.length > 0) {
        for (let i = 0; i < newTableData.length; i++) {
          if (
            newTableData[i].сhoosed_item &&
            Number(newTableData[i].input_item)
          ) {
            top += Number(newTableData[i].input_item);
          }
        }
      }
      console.log(index,field,value)
      setTotalForPayment(top);
      setTableData(newTableData);
    }
  };

  const mobile = window.innerWidth < 600;
  return (
    <Layout>
      <div className="home">
        {!mobile && (
          <>
            <div className="container" style={{ marginTop: "40px" }}>
              <h3>Информация о заемщике</h3>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                  maxWidth: "700px",
                }}
              >
                <DisabledInput label="Имя" text="Оспанов Дамир Маратович" />
                <DisabledInput label="ИИН" text="9601233212233" />
              </div>
            </div>
            <div className="container" style={{ marginTop: "80px" }}>
              <Tabs tabTitles={tabTitles}>
                <Table
                  tableTitle={tableTitles}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />

                <Table
                  tableTitle={tableTitles2}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
              </Tabs>
            </div>{" "}
            <div
              className="container"
              style={{
                marginTop: "80px",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <div style={{ display: "flex", flexDirection: "column" }}>
                <h2>Итого к оплате: {totalForPayment} ₸</h2>
                <p style={{ maxWidth: "430px" }}>
                  * Оплаты принятые после <strong>16:00</strong> в рабочие дни,
                  а также оплаты в <strong>выходные/праздничные</strong> дни
                  будут погашаться следующим днем{" "}
                </p>
              </div>
              <div style={{ width: "300px" }}>
                <Button
                  className="success"
                  label="Перейти к оплате"
                  onClick={() => history.push('/result')}
                  disabled={totalForPayment <= 0}
                />
                <SVG src={TooltipText} />
              </div>
            </div>
            <div className="container" style={{ marginTop: "80px" }}>
              <Accordion faqItems={faqItems} />
            </div>
          </>
        )}

        {mobile && (
          <>
            <div
              className="container"
              style={{
                marginBottom: "50px",
                marginTop: "36px",
                display: "flex",
                justifyContent: "center",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <h3>Информация о заемщике</h3>
                <DisabledInput label="Имя" text="Оспанов Дамир Маратович" />
                <DisabledInput label="ИИН" text="9601233212233" />
              </div>
            </div>
            <div className="container">
              <Tabs tabTitles={tabTitles}>
                <ContractCards
                  tableTitle={tableTitles}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
                <ContractCards
                  tableTitle={tableTitles2}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
              </Tabs>
            </div>
            <div
              className="container"
              style={{
                marginTop: "50px",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                marginBottom: "50px",
              }}
            >
              <h2>Итого к оплате: {totalForPayment} ₸</h2>
              <p style={{ maxWidth: "430px" }}>
                * Оплаты принятые после <strong>16:00</strong> в рабочие дни, а
                также оплаты в <strong>выходные/праздничные</strong> дни будут
                погашаться следующим днем{" "}
              </p>

              <Button
                className="success p-y-15 m-t-50 m-b-30 "
                label="Перейти к оплате"
                onClick={() => history.push('/result')}
                disabled={totalForPayment <= 0}
              />
              <SVG src={TooltipText} />
            </div>
          </>
        )}
      </div>
    </Layout>
  );
};

export default PaymentPage;
